import React, { useState } from 'react';

function CalculationPart() {
  const [Buy, setBuy] = useState(500);
  const [stopLoss, setstopLoss] = useState(450);
  const [maxLoss, setmaxLoss] = useState(1000);
  const diff = Buy - stopLoss
  const target = parseInt(Buy) + 2 * diff;
  const quantity = Math.floor(maxLoss / diff);
  const capital = Buy*quantity

  const buttonOnClick = (e) => {
    e.preventDefault();
  }
  return (
    
    <form className='form-group'>
      <header style={{fontWeight: "bold"}}>Moving Average strategy</header>
      <div style={{color: 'blue'}}>Make sure all 3 fields are correctly entered to get accurate results)</div><br/>
      <label>Enter buy value</label>
      <input
        name='Buy'
        className="form-control"
        placeholder='Enter Buy'
        value={Buy}
        style={{color: 'green'}}
        onChange={(e) => setBuy(e.target.value)}
      /><br />
      <label>Enter Sell value</label>
      <input
        name='Sell'
        className="form-control"
        placeholder='Enter stoploss'
        value={stopLoss}
        style={{color: 'red'}}
        onChange={(e) => setstopLoss(e.target.value)}
      /><br />
      <label>Enter max loss value</label>
      <input
        name='Max loss'
        className="form-control"
        placeholder='Enter max loss you want to bear'
        value={maxLoss}
        onChange={(e) => setmaxLoss(e.target.value)}
      /><br />
      <button
        className="btn btn-default"
        onClick={(e) => buttonOnClick(e)}>
        <span style={{fontWeight: "bold",fontSize: '20px',fontFamily: 'serif'}}>
          Target is {target} and Quantity is {quantity} and Total cash required is {capital}
          </span>
      </button>
    </form>)
}

export default CalculationPart;
